'use strict'

import React, {Component} from 'react';

import Utils from '../utils/Utils';

const style = {
    marginTop: "6px",
    marginBottom: "6px",
    background: "rgba(0,0,0,0)"
}

export default function LatestItem(props){
    return <button className="laserItem" style={style} onClick={props.itemClicked(props.item)}>
                <h6 style={getColor(props.item.laser_type)}>{props.item.full_name} - {Utils.getTime(props.item.created)} - {props.item.sub_admin_address}</h6>
           </button>
}

function getColor(laser_type){
    switch(laser_type){
        case "emergency":
            return {
                color: "#A93226"
            }
        break;
        case "call":
            return {
                color: "#2E86C1"
            }
        break;
        default:
            return {
                color: "#A93226"
            }
        break;
    }
}